@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@200&display=swap);
*{
  padding: 0;
  margin: 0;
  font-family: 'Roboto Slab', serif;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



*{
  text-decoration: none;
}
.Header{
  display: flex;
  width: 90vw;
  justify-content: space-between;
  border-bottom: 1px solid black;
  padding-left: 10vw;
  padding-right: 10vw;
  background-color: #ffc117;
  position: fixed;
}
.HeaderList{
  display: flex;
  list-style-type: none;
}
.HeaderList > li{
 padding: 10px;
 text-decoration: none;
 font-size: 1.5vw;
}
.HeaderList > li > button{
  text-decoration: none;
  border-radius: 25px;
  background-color: black;
  color: white;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 15px;
 }
.MediumLogo{
  width: 15vw;
}
.StayCurious{
    padding-top: 80px;
    height: 70vh;
    text-align: left;
}
.StayCurious > h1{
  font-size: 8vw;
  padding-left: 20px;
}
.StayCurious > p{
    font-size: 2vw;
    padding-left: 20px;
    font-weight: 600;
}
.StayCurious > button{
    margin-top: 30px;
    font-size: 25px;
    width: auto;
    padding-left: 20px;
    padding-right: 20px;
    background-color: black;
    color: white;
    border-radius: 40px;
    margin-left: 10px;
}
.MainContent{
    padding: 40px;
    height: 50vh;
    background-color: #ffc117;
    display: flex;
    justify-content: space-between;
}
.VideoDiv > img{
    height: 45vh;
}
.VideoDiv{
    height: 50vh;
}
.Post{
    display: flex;
    width: 100%;
    padding: 20px 0px;
    align-items: center;
    justify-content: space-around;
}
.PostContainer{
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1190px;
}
.PostTop{
    display: flex;
    flex-direction: row; 
    margin-right: auto; 
    align-items: center;
    font-weight: 600;
}
.RecomendedPostWrapper{
    display: flex;
    width: 100%;
}
.RecomendedPostContent {
    display: flex;
    width: 100%;
  }
  
  .RecomendedPost{
    display: grid;
    margin: 0px 10px;
    /* grid-template-columns: 1fr 1fr 1fr; */
    grid-template-columns: repeat(2, 1r);
    grid-auto-rows: 400px;
    grid-column-gap: 1fr;
    -webkit-column-gap: 1fr;
            column-gap: 1fr;
  }
  
  .RecomendedPost {
    display: flex;
    flex-direction: row;
    cursor: pointer;
    margin: 5px 0px;
  }
  
  .RecomendedPost-left {
    display: flex;
    font-size: 30px;
    color: rgb(216, 216, 216);
    font-weight: 800;
  }
  
  .RecomendedPost-right {
    display: flex;
    width: 100%;
    margin: 10px 0;
  }
  
  .RecomendedPostContent {
    display: flex;
    flex-direction: column;
    margin: 0px 10px;
  }
  
  .RecomendedPostTop {
    display: flex;
    align-items: center;
    width: 100%;
    /* margin: 0px 10px; */
  }
  
  .RecomendedPostTop > img {
    border-radius: 30%;
    width: 20px;
    margin-right: 10px;
    /* margin: 0px 10px; */
  }
  
  .RecomendedPostTop > span {
    font-size: 14px;
    font-weight: 500;
    color: rgba(8, 8, 8, 1);
  }
  
  .Content {
    display: flex;
    width: 100%;
    font-size: 18px;
    margin-top: 10px;
    font-weight: 700;
  }
  
  .Footer {
    display: flex;
    margin: 5px 0;
    color: rgb(175, 175, 175);
  }
  
